import { combineReducers } from 'redux';
import mygramReducer from './mygramReducer';
import adnanfajlurReducer from './adnanfajlurReducer';

export default () => {
  /* ------------- Assemble The Reducers ------------- */
  const rootReducer = combineReducers({
    mygram: mygramReducer,
    home: adnanfajlurReducer,
  });

  return rootReducer;
};



