import Immutable from 'seamless-immutable';
import axios from 'axios';

// name spacing with prefix `mygram/`
const MYGRAM_FAILURE = 'mygram/MYGRAM_FAILURE';
const LOAD_MYGRAM = 'mygram/LOAD_MYGRAM';
const LOAD_MYGRAM_SUCCESS = 'mygram/LOAD_MYGRAM_SUCCESS';

// Create Immutable object
const actionTypes = Immutable({
  MYGRAM_FAILURE,
  LOAD_MYGRAM,
  LOAD_MYGRAM_SUCCESS,
});

const BASE_URL = 'https://www.instagram.com/adnanfajlur/?__a=1';

// export it
export default actionTypes;

/* Action Creator */
export function mygramFailure(error) {
  return {
    type: actionTypes.MYGRAM_FAILURE,
    error,
  };
}

// Mygram
export function loadMygram() {
  return dispatch => {
    axios.get(BASE_URL)
      .then(res => {
        dispatch(loadMygramSuccess(res));
      })
      .catch(err => {
        dispatch(mygramFailure(err));
        return Promise.reject(err);
      });
  };
}

export function loadMygramSuccess(data) {
  return {
    type: actionTypes.LOAD_MYGRAM_SUCCESS,
    data,
  };
}



