import Immutable from 'seamless-immutable';
import actionTypes from '../actions/mygramAction';

export const INITIAL_STATE = Immutable({
  fromAdnan: 'Sample web with original data from instagram, thanks instagram :-)',
  loading: false,
  error: null,
  data: null,
});

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case actionTypes.MYGRAM_FAILURE:
      return state.merge({ error: action.data, loading: false });

    case actionTypes.LOAD_MYGRAM:
      return state.merge({ data: null, loading: true });

    case actionTypes.LOAD_MYGRAM_SUCCESS:
      return state.merge({ data: action.data, loading: false, error: false });

    default:
      return state;
  }
}

export default reducer;



